@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700;900&display=swap);
.item-card {
    display: flex;
    grid-column-gap: 0.8rem;
    -webkit-column-gap: 0.8rem;
            column-gap: 0.8rem;
    align-items: center;
}

.item-card .item-image{
    border: 1px solid rgba(0, 0, 0, 0.25);
    min-width: 90px;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 90px;
}

.item-card .item-image img{
    max-width: 100%;
    max-height:100%;
}

.item-card .item-card-info {
    font-size: 11px; 
}

.item-card .item-card-info .item-name {
    margin-bottom: 4px;
    text-transform: lowercase;
}

.item-card .item-card-info .item-name::first-letter {
   text-transform: uppercase;
}


.item-card .item-card-info .item-price {
    opacity: 0.7;
    font-weight: 500;
    font-size: 0.9em;
}


.item-card .item-card-info .item-selling-price {
    font-weight: 600;
    font-size: 1.1em;
}


* {
  padding: 0;
  margin: 0;
}

body {
  background-color: #d1d8e5;
  font-family: 'Poppins', sans-serif;
  display: block;
  margin: 0px 15px;
}

.app {
  display: flex;
  flex-direction: column;
  max-width: 400px;
  margin: auto;
  height: 100vh;
}

.app-header {
  display: flex;
  grid-column-gap: 3px;
  -webkit-column-gap: 3px;
          column-gap: 3px;
  margin: 15px 0px;
}

.app-header button {
  flex: auto;
  background-color: rgb(1, 167, 139);
  color: white;
  padding: 10px 15px;
  border: none;
  border-radius: 5px;
}
.app-header button:hover {
  opacity: 0.8;
  transition: .3s;
  cursor: pointer;
}

main {
  background-color: white;
  border-radius: 10px;
  box-shadow: 0px 0px 15px 0px #0000002d;
  display: flex;
  margin-bottom: 15px;
  flex-direction: column;
  overflow: auto;
  flex: auto;
}

hr {
  border: none;
  height: 1px;
  background-color: rgba(0, 0, 0, 0.2);
}

#title {
  padding: 12px 0px;
  font-size: 1.1rem;
  font-weight: 700;
  text-align: center;
}

#items-list {
  padding: 20px 15px;
  display: flex;
  flex-direction: column;
  grid-row-gap: 15px;
  row-gap: 15px;
  overflow: auto;
  flex: auto;
}

#finalize-button {
  background-color: #3b74f2;
  padding: 14px 12px;
  margin: 15px;
  font-size: 1.1rem;
  text-shadow: 0px 0px 5px rgba(5, 23, 61, 0.295);
  border: none;
  border-radius: 5px;
  color: white;
  font-stretch: expanded;
  font-weight: 700;
}
#finalize-button:hover {
  background-color: #1080db;
  cursor: pointer;
  transition: .3s;
}
#totals-info {
  display: flex;
  margin: 15px;
  justify-content: space-between;
  font-weight: bold;
  font-size: 1.1rem;
}

.green-alert {
  display: flex;
  justify-content: center;
  margin: 0px 15px 15px 15px;
}

.green-alert p{
  background-color: rgb(199 255 166);
  color: rgb(36 122 4);
  border-radius: 20px;
  padding: 7px 12px;
  font-size: 0.9rem;
  font-weight: 600;
}
